import React from 'react';
import ReactTable from 'react-table';
import PropTypes from 'prop-types';
import 'react-table/react-table.css'

class CustomerTable extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            showCustomerModal: false,
        };
    }

    render() {
        const columns = [{
            Header: 'Name',
            accessor: 'name',
        }, {
            Header: 'Email',
            accessor: 'email',
        }, {
            Header: 'Phone',
            accessor: 'phone'
        }];

        const data = [
            {
                name: 'test',
                email: 'test@test.com',
                phone: '9098978787'
            },
            {
                name: 'test',
                email: 'test@test.com',
                phone: '9098978787'
            },
            {
                name: 'test',
                email: 'test@test.com',
                phone: '9098978787'
            },
            {
                name: 'test',
                email: 'test@test.com',
                phone: '9098978787'
            },
            {
                name: 'test',
                email: 'test@test.com',
                phone: '9098978787'
            }, {
                name: 'test',
                email: 'test@test.com',
                phone: '9098978787'
            },
        ];

        return (
            <ReactTable
                data={data}
                columns={columns}
                defaultPageSize={5}
            />
        );
    }
}

export default CustomerTable;



